// Ù…ÙƒÙˆÙ‘Ù† Ø¨Ø³ÙŠØ· Ù„ØªÙˆØ­ÙŠØ¯ Ø´ÙƒÙ„ Ø§Ù„Ø­Ù‚ÙˆÙ„ Ø¯Ø§Ø®Ù„ Ø§Ù„Ù…ÙˆØ¯Ø§Ù„
function Field({ label, children }) {
  return (
    <div>
      <label className="block mb-1 text-xs font-semibold text-gray-600">
        {label}
      </label>
      {children}
    </div>
  )
}









